.header {
    background-image: url(./../images/head/bg.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;

  &__wrapper {
    max-width: 1720px;
    width: 100%;
    height: 315px;
    padding-top: 90px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }

  &__top {
    max-width: 1720px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /*
    flex-wrap: wrap;
    */
    /*
    margin-top: 90px;
    */
    /*
    margin-bottom: 125px;
    */
  }

  &__nav-bay {
    max-width: 1720px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__burger-btn {
    width: 50px;
    height: 50px;
    padding: 8px;
    /*
    background-color: transparent;
    */
    border: none;
    position: fixed;
    top: 30px;
    right: 30px;
    border-radius: 50%;
    background-color: #0aa566;
    z-index: 2;
    display: none;
  }

  &__burger-btn-icon {
    width: 100%;
  }

  &__nav {
    max-width: 540px;
    width: 100%;
  }

  &__nav-list {
    list-style-type: none;
    display: flex;
    align-items: center;
  }

  &__nav-item:nth-child(n + 1) {
    margin-right: 35px;
  }

  &__nav-item:last-child {
    margin-right: 0;
  }

  &__nav-link {
    text-decoration: none;
    font-family: Muli-Bold;
    font-size: 16px;
    font-weight: bold;
    line-height: 1.2;
    letter-spacing: 0.32px;
    color: #ffffff;
  }

  &__logo {
    position: absolute;
    top: 70px;
    left: 50%;
    transform: translateX(-50%);
    /*
    margin-bottom: 110px;
    */
  }

  &__buy {
    max-width: 420px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__personal-list {
    list-style-type: none;
    display: flex;
    align-items: center;
  }

  &__personal-item:nth-child(n + 1) {
    margin-right: 45px;
  }

  &__personal-item-cart {
    margin-right: 135px;
    position: relative;
  }

  &__span {
    font-family: Muli-Regular;
    font-size: 10px;
    line-height: 1.2;
    letter-spacing: 0.2px;
    color: #000000;
    width: 18px;
    height: 18px;
    border-radius: 50px;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 22px;
    bottom: 17px;
  }

  &__social {
    position: relative;
  }

    &::before {
      content: url(./../images/head/line.png);
      position: absolute;
      left: -85px;
      top: 5px;
    }

  &__social-list {
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__social-item {
    width: 35px;
    height: 35px;
    border-radius: 50px;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__social-item:nth-child(n + 1) {
    margin-right: 10px;
  }

  &__social-item:last-child {
    margin-right: 0;
  }

  &__btn {

    @include btn;


    //        .

    //@include headerBtn;

    max-width: 280px;
    width: 100%;
    height: 70px;
    border: none;
    border-radius: 35px;
    background-color: #0aa566;
    font-size: 24px;
    line-height: 1.2;
    letter-spacing: -0.48px;
    display: flex;
    align-items: center;
    padding-left: 40px;
    position: relative;

    &::after {
      content: "";
      background-image: url(./../images/head/arrow.png);
      background-repeat: no-repeat;
      background-position: right;
      width: 100%;
      height: 100%;
      position: absolute;
      right: 40px;
      top: 0;
    }

    &:hover {
      &::after {
        transform: translate(20px);
        transition: 1.2s;
      }
    }
    
    &:hover {
      background-color: #30aa30;
      border: 2px solid white;
    }
  }
}
